<template>
  <view class="edit-profile-page" :style="{ paddingTop: containerPaddingTop }">
    <!-- Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
    <view class="custom-navbar">
      <!-- Ê∏êÂèòËÉåÊôØ -->
      <view class="navbar-gradient-bg"></view>
      <!-- Áä∂ÊÄÅÊ†èÂç†‰Ωç -->
      <view class="status-bar" :style="{ height: statusBarHeight + 'px' }"></view>
      <!-- ÂØºËà™Ê†èÂÜÖÂÆπ -->
      <view class="navbar-content" :style="{ height: navBarHeight + 'px' }">
        <view class="navbar-left" @click="goBack">
          <text class="back-icon">‚Üê</text>
        </view>
        <view class="navbar-title">
          <text class="title-text">ÁºñËæëËµÑÊñô</text>
        </view>
        <view class="navbar-right"></view>
      </view>
    </view>

    <!-- ÂÜÖÂÆπÂå∫Âüü -->
    <view class="content">
      <!-- Â§¥ÂÉèÁºñËæëÂå∫Âüü -->
      <view class="section avatar-section">
        <text class="section-title">Â§¥ÂÉè</text>
        <view class="avatar-container">
          <view class="current-avatar-wrapper">
            <image 
              class="current-avatar" 
              :src="tempAvatar || userInfo.displayAvatar || userInfo.avatarUrl || '/static/login/love.jpg'" 
              mode="aspectFill"
            />
          </view>
          <view class="avatar-actions">
            <button class="avatar-btn" @click="chooseAvatar">
              <text class="btn-icon">üì∑</text>
              <text class="btn-text">Êõ¥Êç¢Â§¥ÂÉè</text>
            </button>
          </view>
        </view>
      </view>

      <!-- ÊòµÁß∞ÁºñËæëÂå∫Âüü -->
      <view class="section nickname-section">
        <text class="section-title">ÊòµÁß∞</text>
        <view class="nickname-container">
          <view class="input-wrapper">
            <input 
              class="nickname-input" 
              v-model="tempNickname" 
              placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞"
              maxlength="20"
              @input="onNicknameInput"
            />
            <text class="char-count">{{ nicknameCharCount }}/20</text>
          </view>
          <view class="nickname-tips">
            <text class="tips-text">ÊîØÊåÅ‰∏≠Ëã±Êñá„ÄÅÊï∞Â≠óÔºå2-20‰∏™Â≠óÁ¨¶</text>
          </view>
        </view>
      </view>

      <!-- ‰øùÂ≠òÊåâÈíÆ -->
      <view class="save-section">
        <button 
          class="save-btn" 
          :disabled="isSaving" 
          @click="saveProfile"
        >
          <text class="save-icon">üíæ</text>
          <text class="save-text">{{ isSaving ? '‰øùÂ≠ò‰∏≠...' : '‰øùÂ≠òËµÑÊñô' }}</text>
        </button>
      </view>
    </view>
  </view>
</template>

<script>
import { updateUserProfile } from '@/api/user.js';
import { getUserInfo, saveLoginInfo } from '@/utils/auth.js';
import config from '@/utils/config.js';

export default {
  data() {
    return {
      statusBarHeight: 0,
      navBarHeight: 44,
      screenWidth: 375,
      userInfo: {
        nickName: '',
        avatarUrl: '',
        displayName: '',
        displayAvatar: ''
      },
      tempNickname: '',
      tempAvatar: '',
      isSaving: false
    };
  },
  computed: {
    containerPaddingTop() {
      const totalHeightPx = this.statusBarHeight + this.navBarHeight;
      const pxToRpx = 750 / this.screenWidth;
      const totalHeightRpx = totalHeightPx * pxToRpx;
      return totalHeightRpx + 'rpx';
    },
    nicknameCharCount() {
      return this.tempNickname.length;
    }
  },
  onLoad() {
    this.getSystemInfo();
    this.loadUserInfo();
  },
  methods: {
    goBack() {
      uni.navigateBack();
    },
    getSystemInfo() {
      // #ifdef MP-WEIXIN
      try {
        const windowInfo = wx.getWindowInfo && wx.getWindowInfo();
        const deviceInfo = wx.getDeviceInfo && wx.getDeviceInfo();
        
        if (windowInfo && deviceInfo) {
          this.statusBarHeight = windowInfo.statusBarHeight || 0;
          this.screenWidth = windowInfo.windowWidth || 375;
        } else {
          const sysInfo = uni.getSystemInfoSync();
          this.statusBarHeight = sysInfo.statusBarHeight || 0;
          this.screenWidth = sysInfo.windowWidth || 375;
        }
      } catch (e) {
        const sysInfo = uni.getSystemInfoSync();
        this.statusBarHeight = sysInfo.statusBarHeight || 0;
        this.screenWidth = sysInfo.windowWidth || 375;
      }
      this.navBarHeight = 44;
      // #endif
      // #ifdef H5
      const sysInfoH5 = uni.getSystemInfoSync();
      this.statusBarHeight = sysInfoH5.statusBarHeight || 0;
      this.screenWidth = sysInfoH5.windowWidth || 375;
      this.navBarHeight = 44;
      // #endif
      // #ifndef MP-WEIXIN || H5
      const sysInfoOther = uni.getSystemInfoSync();
      this.statusBarHeight = sysInfoOther.statusBarHeight || 0;
      this.screenWidth = sysInfoOther.windowWidth || 375;
      this.navBarHeight = 44;
      // #endif
    },
    loadUserInfo() {
      try {
        const userInfoData = getUserInfo();
        if (userInfoData) {
          this.userInfo = { ...userInfoData };
          // ÂàùÂßãÂåñ‰∏¥Êó∂ÊòµÁß∞‰∏∫ÂΩìÂâçÊòæÁ§∫ÊòµÁß∞
          this.tempNickname = this.userInfo.displayName || this.userInfo.nickName || '';
        }
      } catch (error) {
        console.error('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•', error);
      }
    },
    onNicknameInput(e) {
      this.tempNickname = e.detail.value;
    },
    chooseAvatar() {
      // ‰ΩøÁî®ÂæÆ‰ø°Â∞èÁ®ãÂ∫èÁöÑ chooseMedia API ÈÄâÊã©ÂõæÁâá
      // #ifdef MP-WEIXIN
      wx.chooseMedia({
        count: 1,
        mediaType: ['image'],
        sourceType: ['album', 'camera'],
        success: (res) => {
          const tempFilePath = res.tempFiles[0].tempFilePath;
          this.uploadAvatar(tempFilePath);
        },
        fail: (err) => {
          console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•', err);
          // ÈôçÁ∫ßÂà∞ chooseImage
          this.chooseImageFallback();
        }
      });
      // #endif
      
      // #ifndef MP-WEIXIN
      this.chooseImageFallback();
      // #endif
    },
    chooseImageFallback() {
      uni.chooseImage({
        count: 1,
        sourceType: ['album', 'camera'],
        success: (res) => {
          const tempFilePath = res.tempFilePaths[0];
          this.uploadAvatar(tempFilePath);
        },
        fail: (err) => {
          console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•', err);
          uni.showToast({
            title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
            icon: 'none'
          });
        }
      });
    },
    async uploadAvatar(filePath) {
      uni.showLoading({
        title: '‰∏ä‰º†‰∏≠...'
      });
      
      try {
        console.log('üì§ [‰∏ä‰º†Â§¥ÂÉè] ÂºÄÂßã‰∏ä‰º†ÔºåÊñá‰ª∂Ë∑ØÂæÑ:', filePath);
        
        // È™åËØÅÊñá‰ª∂Ë∑ØÂæÑ
        if (!filePath) {
          throw new Error('Êú™ÈÄâÊã©Êñá‰ª∂');
        }
        
        // Ëé∑ÂèñtokenÔºàÊîπËøõÁöÑtokenËé∑ÂèñÊñπÂºèÔºâ
        let token = uni.getStorageSync('token');
        if (!token) {
          // Â¶ÇÊûúÊ≤°ÊúâÁõ¥Êé•Â≠òÂÇ®ÁöÑtokenÔºåÂ∞ùËØï‰ªélogin_info‰∏≠Ëé∑Âèñ
          const loginInfo = uni.getStorageSync('login_info');
          if (loginInfo && loginInfo.token) {
            token = loginInfo.token;
          } else if (loginInfo && loginInfo.data && loginInfo.data.token) {
            token = loginInfo.data.token;
          }
        }
        
        console.log('üîë [‰∏ä‰º†Â§¥ÂÉè] Token:', token ? `${token.substring(0, 20)}...` : 'Êú™ÊâæÂà∞');
        
        // È™åËØÅtokenÊòØÂê¶Â≠òÂú®
        if (!token) {
          throw new Error('Êú™ÊâæÂà∞ÁôªÂΩïÂá≠ËØÅÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï');
        }
        
        // ‰ΩøÁî® uni.uploadFile ‰∏ä‰º†ÂõæÁâá
        const uploadResult = await new Promise((resolve, reject) => {
          uni.uploadFile({
            url: config.baseURL + config.API.USER.AVATAR_UPLOAD,
            filePath: filePath,
            name: 'file',
            header: {
              'Authorization': `Bearer ${token}`  // Á°Æ‰øù‰ΩøÁî®BearerÂâçÁºÄ
            },
            success: (uploadRes) => {
              console.log('üì• [‰∏ä‰º†Â§¥ÂÉè] ‰∏ä‰º†ÊàêÂäüÔºåÂìçÂ∫î:', uploadRes);
              resolve(uploadRes);
            },
            fail: (uploadErr) => {
              console.error('‚ùå [‰∏ä‰º†Â§¥ÂÉè] ‰∏ä‰º†Â§±Ë¥•ÔºåÈîôËØØ:', uploadErr);
              reject(uploadErr);
            }
          });
        });
        
        // Ê£ÄÊü•ÂìçÂ∫îÁä∂ÊÄÅ
        if (uploadResult.statusCode !== 200) {
          throw new Error(`‰∏ä‰º†Â§±Ë¥•ÔºåÊúçÂä°Âô®ËøîÂõûÁä∂ÊÄÅÁ†Å: ${uploadResult.statusCode}`);
        }
        
        // Ëß£Êûê‰∏ä‰º†ÁªìÊûú
        let data;
        try {
          data = JSON.parse(uploadResult.data);
          console.log('üì• [‰∏ä‰º†Â§¥ÂÉè] Ëß£ÊûêÂêéÁöÑÊï∞ÊçÆ:', data);
        } catch (parseError) {
          // Â¶ÇÊûúJSONËß£ÊûêÂ§±Ë¥•ÔºåÁõ¥Êé•‰ΩøÁî®ÂéüÂßãÊï∞ÊçÆ
          console.error('‚ùå [‰∏ä‰º†Â§¥ÂÉè] JSONËß£ÊûêÂ§±Ë¥•Ôºå‰ΩøÁî®ÂéüÂßãÊï∞ÊçÆ:', uploadResult.data);
          data = uploadResult.data;
        }
        
        // Ê£ÄÊü•ÂìçÂ∫îÊï∞ÊçÆÁªìÊûÑÂπ∂ÊèêÂèñÂõæÁâáURL
        let photoUrl = null;
        
        // Ê†πÊçÆ‰∏çÂêåÁöÑÂìçÂ∫îÊ†ºÂºèÊèêÂèñÂõæÁâáURL
        if (data && typeof data === 'object') {
          // Ê†ºÂºè1: { code: 200, imgUrl: "..." }
          if (data.code === 200 && data.imgUrl) {
            photoUrl = data.imgUrl;
          }
          // Ê†ºÂºè2: { code: 200, data: { imgUrl: "..." } }
          else if (data.code === 200 && data.data && typeof data.data === 'object' && data.data.imgUrl) {
            photoUrl = data.data.imgUrl;
          }
          // Ê†ºÂºè3: { code: 200, data: "..." } (dataÁõ¥Êé•ÊòØURL)
          else if (data.code === 200 && data.data && typeof data.data === 'string' && data.data.includes('http')) {
            photoUrl = data.data;
          }
          // Ê†ºÂºè4: { photoUrl: "..." }
          else if (data.photoUrl) {
            photoUrl = data.photoUrl;
          }
          // Ê†ºÂºè5: { url: "..." }
          else if (data.url) {
            photoUrl = data.url;
          }
          // Ê†ºÂºè6: { data: { photoUrl: "..." } }
          else if (data.data && typeof data.data === 'object' && data.data.photoUrl) {
            photoUrl = data.data.photoUrl;
          }
          // Ê†ºÂºè7: { data: { url: "..." } }
          else if (data.data && typeof data.data === 'object' && data.data.url) {
            photoUrl = data.data.url;
          }
        } else if (typeof data === 'string' && data.includes('http')) {
          // Ê†ºÂºè8: Áõ¥Êé•ËøîÂõûURLÂ≠óÁ¨¶‰∏≤
          photoUrl = data;
        }
        
        if (photoUrl) {
          this.tempAvatar = photoUrl; // ‰øùÂ≠òÊñ∞Â§¥ÂÉèURL
          console.log('‚úÖ [‰∏ä‰º†Â§¥ÂÉè] ‰∏ä‰º†ÊàêÂäüÔºåÂõæÁâáURL:', photoUrl);
          
          // Âº∫Âà∂Âà∑Êñ∞ÂõæÁâáÊòæÁ§∫
          this.$forceUpdate();
          
          // Ê∑ªÂä†Êó∂Èó¥Êà≥Èò≤Ê≠¢ÁºìÂ≠ò
          const timestamp = new Date().getTime();
          this.tempAvatar = photoUrl + '?t=' + timestamp;
          
          uni.showToast({
            title: '‰∏ä‰º†ÊàêÂäü',
            icon: 'success'
          });
        } else {
          // ‰ªéÂìçÂ∫î‰∏≠ÊèêÂèñÈîôËØØÊ∂àÊÅØ
          const errorMsg = (data && typeof data === 'object') ? 
            (data.message || data.msg || data.errorMessage || '‰∏ä‰º†Â§±Ë¥•') : 
            '‰∏ä‰º†Â§±Ë¥•';
          throw new Error(errorMsg || '‰∏ä‰º†Â§±Ë¥•');
        }
      } catch (error) {
        console.error('‰∏ä‰º†Â§¥ÂÉèÂ§±Ë¥•', error);
        // ÊòæÁ§∫Êõ¥ËØ¶ÁªÜÁöÑÈîôËØØ‰ø°ÊÅØ
        const errorMessage = error.message || '‰∏ä‰º†Â§±Ë¥•';
        uni.showToast({
          title: errorMessage,
          icon: 'none'
        });
      } finally {
        uni.hideLoading();
      }
    },
    async saveProfile() {
      if (this.isSaving) return;
      
      // Ê£ÄÊü•ÊòµÁß∞ÊòØÂê¶‰∏∫Á©∫
      if (!this.tempNickname.trim()) {
        uni.showToast({
          title: 'ÊòµÁß∞‰∏çËÉΩ‰∏∫Á©∫',
          icon: 'none'
        });
        return;
      }
      
      this.isSaving = true;
      
      try {
        // Ë∞ÉÁî®APIÊõ¥Êñ∞Áî®Êà∑ËµÑÊñô
        const response = await updateUserProfile(
          this.tempNickname, 
          this.tempAvatar || this.userInfo.displayAvatar || this.userInfo.avatarUrl
        );
        
        if (response && response.code === 200) {
          // Êõ¥Êñ∞Êú¨Âú∞Áî®Êà∑‰ø°ÊÅØ
          const updatedUserInfo = {
            ...this.userInfo,
            displayName: this.tempNickname,
            displayAvatar: this.tempAvatar || this.userInfo.displayAvatar || this.userInfo.avatarUrl
          };
          
          // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®Ôºà‰øùÁïôtokenÔºâ
          saveLoginInfo(updatedUserInfo);
          
          // Êõ¥Êñ∞ÂÖ®Â±ÄÁî®Êà∑‰ø°ÊÅØ
          this.userInfo = updatedUserInfo;
          
          uni.showToast({
            title: '‰øùÂ≠òÊàêÂäü',
            icon: 'success'
          });
          
          // ËøîÂõû‰∏ä‰∏ÄÈ°µ
          setTimeout(() => {
            uni.navigateBack();
          }, 1000);
        } else {
          throw new Error(response?.message || '‰øùÂ≠òÂ§±Ë¥•');
        }
      } catch (error) {
        console.error('‰øùÂ≠òÁî®Êà∑ËµÑÊñôÂ§±Ë¥•', error);
        uni.showToast({
          title: error.message || '‰øùÂ≠òÂ§±Ë¥•',
          icon: 'none'
        });
      } finally {
        this.isSaving = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.edit-profile-page {
  min-height: 100vh;
  background-color: #FFFAF4;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', sans-serif;
}

/* Ëá™ÂÆö‰πâÂØºËà™Ê†èÊ†∑Âºè */
.custom-navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  background-color: #FFFAF4;
  overflow: hidden;
}

.navbar-gradient-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 200%;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
}

.status-bar {
  width: 100%;
  background: transparent;
  position: relative;
  z-index: 1;
}

.navbar-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30rpx;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

.navbar-title {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.title-text {
  font-size: 40rpx; /* Â¢ûÂ§ßÂ≠ó‰Ωì */
  font-weight: 500;
  color: #4A4A4A;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', sans-serif;
}

.navbar-left {
  width: 80rpx;
  display: flex;
  align-items: center;
  justify-content: center;
}

.back-icon {
  font-size: 50rpx; /* Â¢ûÂ§ßÂ≠ó‰Ωì */
  font-weight: 600;
  color: #4A4A4A;
  line-height: 1;
  cursor: pointer;
  transition: opacity 0.3s;
}

.back-icon:active {
  opacity: 0.6;
}

.navbar-right {
  width: 80rpx;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ÂÜÖÂÆπÂå∫Âüü */
.content {
  padding: 15rpx; /* ÂáèÂ∞ëÁïôÁôΩ */
  padding-top: calc(15rpx + 44px); /* ÂáèÂ∞ëÂØºËà™Ê†è‰∏ãÊñπÁïôÁôΩ */
}

/* Âå∫ÂùóÊ†∑Âºè */
.section {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border-radius: 16rpx;
  padding: 20rpx; /* ÂáèÂ∞ëÂÜÖËæπË∑ù */
  margin-bottom: 10rpx; /* ÂáèÂ∞ëÂå∫ÂùóÈó¥Ë∑ù */
  box-shadow: 0 8rpx 12rpx rgba(0, 0, 0, 0.04), inset 0 0 0 2rpx rgba(255,255,255,0.5);
}

.section-title {
  display: block;
  font-size: 34rpx; /* Â¢ûÂ§ßÊ†áÈ¢òÂ≠ó‰Ωì */
  font-weight: 600;
  color: #333;
  margin-bottom: 12rpx; /* ÂáèÂ∞ëÊ†áÈ¢ò‰∏ãÊñπÁïôÁôΩ */
}

/* Â§¥ÂÉèÁºñËæëÂå∫Âüü */
.avatar-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30rpx;
}

.current-avatar-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16rpx;
}

.current-avatar {
  width: 120rpx;
  height: 120rpx;
  border-radius: 60rpx;
  border: 4rpx solid #ffffff;
  box-shadow: 0 8rpx 20rpx rgba(0, 0, 0, 0.1);
}

.avatar-actions {
  width: 100%;
}

.avatar-btn {
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6rpx;
  padding: 8rpx 16rpx;
  background: linear-gradient(135deg, #ffd166 0%, #ff9ebc 100%);
  border-radius: 20rpx;
  border: none;
  font-size: 20rpx;
  color: #ffffff;
  cursor: pointer;
  box-shadow: 0 2rpx 8rpx rgba(255, 158, 188, 0.25);
  white-space: nowrap;
}

.avatar-btn:active {
  opacity: 0.85;
}

.btn-icon {
  font-size: 20rpx;
}

.btn-text {
  font-size: 20rpx;
}

/* ÊòµÁß∞ÁºñËæëÂå∫Âüü */
.nickname-container {
  display: flex;
  flex-direction: column;
  gap: 16rpx;
}

.input-wrapper {
  position: relative;
}

.nickname-input {
  width: 100%;
  padding: 28rpx; /* Â¢ûÂ§ßÂÜÖËæπË∑ùÔºå‰ΩøËæìÂÖ•Ê°ÜÊõ¥È´ò */
  padding-right: 80rpx; /* ‰∏∫Â≠óÁ¨¶ËÆ°Êï∞ÁïôÂá∫Á©∫Èó¥ */
  border: 1rpx solid #e5e5e5;
  border-radius: 12rpx;
  font-size: 32rpx; /* Â¢ûÂ§ßÂ≠ó‰Ωì */
  background: #ffffff;
  box-sizing: border-box;
}

.char-count {
  position: absolute;
  right: 20rpx;
  top: 50%;
  transform: translateY(-50%);
  font-size: 28rpx; /* Â¢ûÂ§ßÂ≠ó‰ΩìÔºå‰∏éËæìÂÖ•Ê°ÜÂåπÈÖç */
  color: #999;
}

.nickname-tips {
  margin-top: 8rpx;
}

.tips-text {
  font-size: 24rpx; /* Â¢ûÂ§ßÊèêÁ§∫ÊñáÂ≠ó */
  color: #999;
}

/* ‰øùÂ≠òÊåâÈíÆ */
.save-section {
  margin-top: 30rpx; /* ÂáèÂ∞ëÈ°∂ÈÉ®Èó¥Ë∑ù */
}

.save-btn {
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6rpx;
  padding: 12rpx 24rpx;
  background: linear-gradient(135deg, #ffd166 0%, #ff9ebc 100%);
  border-radius: 20rpx;
  border: none;
  color: #ffffff;
  font-size: 20rpx;
  font-weight: 600;
